<template>
  <div class="notesdetail">
    <!-- 导航栏 -->
    <div class="nav">
      <div class="nav-left">
        <div class="back" @click="back">
          <van-icon size="20" name="arrow-left" />
        </div>
        <div class="userInfo">
          <div class="head-img">
            <img
              class="auto-img"
              :style="{ alignText: 'center' }"
              :src="author.p"
              alt=""
            />
          </div>
          <div class="uesrname-box">
            <div class="username">{{ author.n }}</div>
            <div class="daka">{{ data.straight_text }}</div>
          </div>
          <div class="lv">LV.{{ author.lvl }}</div>
        </div>
      </div>
      <div class="nav-right">
        <div class="follow interest" @click="interest">关注</div>
        <div class="more">
          <van-icon @click="showShare = true" name="ellipsis" />
        </div>
      </div>
    </div>

    <!-- 内容 -->
    <div class="main">
      <div class="banner">
        <van-swipe :autoplay="3000" indicator-color="#fff">
          <van-swipe-item
            class="my-swipe"
            v-for="(img, index) in swiper"
            :key="index"
          >
            <img class="auto-img" :src="img.u" alt="" />
          </van-swipe-item>
        </van-swipe>
        <div class="banner-text" v-show="!data.selection_text">
          {{ data.selection_text }}
        </div>
      </div>
      <div class="title">{{ data.title }}</div>
      <div class="content">
        <span
          class="content-item"
          :style="{ color: item.color }"
          v-for="(item, index) in content"
          :key="index"
        >
          {{ item.c }}
        </span>
        <span v-if="c.length">{{ c }}</span>
        <span v-else></span>
      </div>
    </div>
    <div class="date-box">
      <div class="date">{{ date }}</div>
      <div class="likes">{{ data.like_count }}次赞</div>
    </div>
    <!-- 相关笔记 -->
    <div class="related-notes-title">相关笔记</div>
    <waterfall
      :col="col"
      :width="itemWidth"
      :gutterWidth="gutterWidth"
      :data="moreData"
      @loadmore="loadmore"
    >
      <template>
        <div
          class="related-notes"
          v-for="(item, index) in moreData"
          :key="index"
          @click="GitDetail(item)"
        >
          <div class="banner">
            <img class="auto-img" :src="item.note.image_u" alt="" />
          </div>
          <div class="title">{{ item.note.title }}</div>
          <div class="user-likes">
            <div class="user">
              <div class="head-img">
                <img class="auto-img" :src="item.note.author.p" alt="" />
              </div>
              <div class="name">{{ item.note.author.n }}</div>
            </div>
            <div class="likes">
              <div class="likes-img" @click="dz(index, item)">
                <img
                  class="auto-img"
                  ref="itemimg"
                  src="../assets/icon/love.png"
                  alt=""
                />
              </div>
              <div class="likes-count">{{ item.note.like_count }}</div>
            </div>
          </div>
        </div>
      </template>
    </waterfall>
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      @select="onSelect"
    />
    <!-- 输入评论 -->
    <div class="input-comment">
      <div class="input-box">
        <input
          @keyup.enter="upDataPop"
          class="input"
          type="text"
          v-model="inpValuet"
          placeholder="说点什么..."
        />
      </div>
      <div class="comment-count">
        <div class="comment-img" @click="gitOverlay">
          <img class="auto-img" src="../assets/guanzhu/pinglun.png" alt="" />
        </div>
        <van-popup
          v-model="show"
          closeable
          position="bottom"
          :style="{ height: '60%' }"
        >
          <div class="pup-box" v-if="!pupData.length == '0'">
            <div class="qbpl">
              <div class="left-text">全部评论({{ pupData.length }})</div>
            </div>
            <van-divider />
            <div class="con-box" v-for="(da, index) in pupData" :key="index">
              <div class="n-img">
                <img
                  class="auto-img"
                  src="../assets/icon/head.png"
                  alt=""
                />
              </div>
              <div class="con-text">
                <div class="con-top">
                  <div class="con-name">{{ da.name }}</div>
                  <div class="con-lv">{{ da.lv }}</div>
                  <div class="con-zan">
                    <div class="con-img"  @click="dzt(da,index)">
                      <img
                        class="auto-img"
						            ref="daimg"
                        src="../assets/icon/love.png"
                        alt=""
                      />
                    </div>
                    <div class="img-text">{{ da.countd }}赞</div>
                  </div>
                </div>
                <div class="rqdz">
                  <div class="con-crty">{{ da.city }}</div>
                  <div class="con-data">{{ da.DateD }}</div>
                </div>

                <div class="con-tile">{{ da.value }}</div>
              </div>
            </div>
            <div class="input-box2">
              <input
                @keyup.enter="upDataPop"
                class="input2"
                type="text"
                v-model="inpValuet"
                placeholder="我想说两句"
              />
            </div>
          </div>
          <div v-else>
            <div class="qbpl">
              <div class="left-text">全部评论({{ pupData.length }})</div>
            </div>
            <van-empty description="暂无评论" />
            <div class="input-box2">
              <input
                @keyup.enter="upDataPop"
                class="input2"
                type="text"
                v-model="inpValuet"
                placeholder="我想说两句"
              />
            </div>
          </div>
        </van-popup>
        <div class="count">评论</div>
      </div>
      <div class="likes-box">
        <div class="likes-img" @click="dzo">
          <img
            class="auto-img"
            ref="dzimg"
            src="../assets/icon/love.png"
            alt=""
          />
        </div>
        <div class="count">{{ data.like_count }}</div>
      </div>
      <div class="collect-box"  @click="goLogin">
        <div class="collect-img">
          <img class="auto-img collectimg" src="../assets/icon/collect.png" alt="" />
        </div>
        <div class="text">收藏</div>
      </div>
    </div>
  </div>
</template>

<script>
import { Dialog } from 'vant'
import { Toast } from 'vant'
import getCurrentCityName from '../util/getUserLocation'
export default {
  name: 'Notesdetail',
  data() {
    return {
      isDz: false,
	    isDzt:false,
      show: false,
      inpValuet: '',
      id: '',
      author: {},
      data: {},
      content: [],
      c: [],
      swiper: [],
      date: '',
      rfsData: [],
      moreData: [],
      pupData: [],
      offset: 0,
      limit: 20,
      dqwz: '',
      col: 2,
      page: 1,
      isImg: false,
      showShare: false,
      uname: '',
      isImg: false,
      options: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '朋友圈', icon: 'wechat-moments' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' },
        ],
        [
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' },
          { name: '小程序码', icon: 'weapp-qrcode' },
        ],
      ],
    }
  },
  created() {
    this.id = this.$route.params.id
    this.getNotesDetailData()
    this.getTake()
  },
  computed: {
    itemWidth() {
      return 350 * 0.5 * (document.documentElement.clientWidth / 375) // #rem布局 计算宽度
    },
    gutterWidth() {
      return 9 * 0.5 * (document.documentElement.clientWidth / 375) //#rem布局 计算x轴方向margin(y轴方向的margin自定义在css中即可)
    },
  },

  mounted() {
    this.getCurrentCity()
  },

  methods: {

    GitDetail(e){
       (e.note.id);
      this.$router.push({name:'Notesdetail',params:{id:e.note.id}})
    },

    goLogin() {
       (this.id);
      let collectImg = document.querySelector('.collectimg')
      let text = document.querySelector('.text')
      if (!this.$root.isLogin) {
        this.$router.push({ name: 'Login' })
      } else {
        if (collectImg.src === require('../assets/icon/collect.png')) {
          collectImg.src = require('../assets/icon/liangwujiaoxing.png')
          text.innerHTML = '已收藏'
          for(var i = 0;i<this.$root.scjl.length;i++){
            if(this.id !== this.$root.scjl[i]){
              this.$root.scjl.push(this.id);
              break;
            }else{
              if (this.id === this.$root.scjl[i]) {
                this.$root.scjl.splice(i, 1)
              }
            }
          }
          Toast('收藏成功')
        } else {
          collectImg.src = require('../assets/icon/collect.png')
          text.innerHTML = '收藏'
          for(var i = 0;i<this.$root.scjl.length;i++){
            if(this.id === this.$root.scjl[i]){
              this.$root.scjl.splice(i,1);
            }
          }
          Toast('取消收藏')
        }
      }
    },

    dzt(e,i) {
      if (!this.$root.isLogin) {
        this.$router.push({ name: 'Login' })
      } else {
        if (!this.isDzt) {
          this.$refs.daimg[i].src = require('../assets/icon/love-active.png')
          Toast('点赞成功')
          this.isDzt = true
          e.countd =parseInt(e.countd + 1)
        } else {
          this.$refs.daimg[i].src = require('../assets/icon/love.png')
          Toast('取消点赞')
          this.isDzt = false
          e.countd = parseInt(e.countd - 1)
        }
      }
    },

    gitOverlay() {
      this.show = true
    },

    getCurrentCity() {
      getCurrentCityName().then((crity) => {
        this.dqwz = crity
      })
    },

    upDataPop() {
      if (this.$root.isLogin) {
        let data = localStorage.getItem('data')
        let dataJson = JSON.parse(data)
        this.uname = dataJson.username
        var date = new Date()
        let y = date.getFullYear() //获取完整的年份(4位)
        let m = date.getMonth() + 1 //获取当前月份(0-11,0代表1月)
        let d = date.getDate() //获取当前日(1-31)
        m = m < 10 ? '0' + m : m
        d = d < 10 ? '0' + d : d
        this.pupData.push({
          name: this.uname,
          lv: 'lv1',
          DateD: y + '-' + m + '-' + d,
          city: this.dqwz.name,
          value: this.inpValuet,
          countd: '',
        })
        this.inpValuet = ''
      } else {
        Toast('请先登入')
        this.$router.push({ name: 'Login' })
      }
    },

    dzo() {
      if (!this.$root.isLogin) {
        this.$router.push({ name: 'Login' })
      } else {
        if (!this.isDz) {
          this.$refs.dzimg.src = require('../assets/icon/love-active.png')
          Toast('点赞成功')
          this.isDz = true
          this.data.like_count = this.data.like_count + 1
        } else {
          this.$refs.dzimg.src = require('../assets/icon/love.png')
          Toast('取消点赞')
          this.isDz = false
          this.data.like_count = this.data.like_count - 1
        }
      }
    },

    dz(index, item) {
      if (!this.$root.isLogin) {
        this.$router.push({ name: 'Login' })
      } else {
        if (!this.isImg) {
          this.$refs.itemimg[
            index
          ].src = require('../assets/icon/love-active.png')
          item.note.like_count = item.note.like_count + 1
          Toast('点赞成功')
          this.isImg = true
        } else {
          this.$refs.itemimg[index].src = require('../assets/icon/love.png')
          item.note.like_count = item.note.like_count - 1
          Toast('取消点赞')
          this.isImg = false
        }
      }
    },

    // 返回
    back() {
      this.$router.go(-1)
    },
    loadmore() {
      this.offset += this.limit + 10
      this.axios({
        method: 'get',
        url:
          'https://apis.netstart.cn/douguo/note/related/' +
          this.id +
          '/' +
          this.offset +
          '/' +
          this.limit,
      })
        .then((result) => {
          result.data.result.list.forEach((e) => {
            this.moreData.push(e)
          })
        })
        .catch((err) => {})
    },
    // 获取数据
    getNotesDetailData() {
      this.axios({
        method: 'get',
        url: 'https://apis.netstart.cn/douguo/note/detail/' + this.id,
      })
        .then((result) => {
          if (result.status === 200) {
            this.data = result.data.result.note 
            this.author = result.data.result.note.author
            this.date = result.data.result.note.publishtime.split(' ')[0]
            result.data.result.note.contents.forEach((e) => {
              if (e.type === 3) {
                this.content.push(e)
              }
              if (e.type === 0) {
                this.c = e.c
              }
            })
            this.swiper = result.data.result.note.images
          }
        })
        .catch((err) => {})
    },

    // 瀑布流
    getTake() {
      this.axios({
        method: 'get',
        url:
          'https://apis.netstart.cn/douguo/note/related/' +
          this.id +
          '/' +
          this.offset +
          '/' +
          this.limit,
      })
        .then((result) => {
          this.moreData = []
          this.moreData = result.data.result.list
        })
        .catch((err) => {})
    },
    goNotesDetail(id) {
      this.$router.push({ name: 'Notesdetail', params: { id } })
    },

    // 关注
    interest() {
      let interest = document.querySelector('.interest')
      if (!this.$root.isLogin) {
        this.$router.push({ name: 'Login' })
      } else {
        if (!this.isinterest) {
          interest.innerText = '已关注'
          interest.style.backgroundColor = '#f1f1f1'
          this.isinterest = true
        } else {
          Dialog.confirm({
            title: '提示',
            message: '确定要取消关注ta吗?',
          }).then(() => {
            interest.innerText = '关注'
            interest.style.backgroundColor = '#facc30'
          })
          this.isinterest = false
        }
      }
    },

    onSelect(option) {
      Toast(option.name)
      this.showShare = false
    },
  },
}
</script>

<style lang="less" scoped>
.con-box {
  display: flex;
  padding: 0 10px 10px 10px;
  .n-img {
    width: 30px;
    height: 30px;
    img {
      border-radius: 50%;
    }
  }
  .con-text {
    margin-left: 8px;
    flex: 2;
    .con-top {
      display: flex;
      align-items: center;
      .con-lv {
        margin-left: 2px;
        font-family: monospace;
        color: #ffc536;
      }
      .con-zan {
        display: flex;
        margin-left: auto;
        align-items: center;
        .con-img {
          width: 15px;
          height: 15px;
          margin-right: 2px;
        }
      }
    }
  }
}

.rqdz {
  display: flex;
  align-items: center;
  padding: 5px 0;
  color: #666;
  .con-data {
    margin-left: 5px;
  }
}

.qbpl {
  padding: 18px;
  .left-text {
    font-size: 16px;
  }
}

.input-box2 {
  position: absolute;
  width: calc(100% - 30px);
  left: 15px;
  bottom: 10px;
  .input2 {
    width: calc(100% - 30px);
    outline: none;
    border: none;
    background-color: #ddd;
    border-radius: 30px;
    padding: 8px 15px;
    font-size: 14px;
  }
}

.notesdetail {
  padding-top: 55px;
  padding-bottom: 50px;
  .nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 55px;
    background-color: #fff;
    overflow: hidden;
    display: flex;
    z-index: 99;
    align-items: center;
    padding: 0 10px;
    .nav-left {
      display: flex;
      align-items: center;
      .userInfo {
        display: flex;
        margin-left: 15px;
        align-items: center;
        .head-img {
          width: 35px;
          height: 35px;
          border-radius: 50%;
          overflow: hidden;
        }
        .uesrname-box {
          margin: 0 5px;
          .uesrname {
            font-size: 12px;
          }
          .daka {
            font-size: 12px;
            color: #666;
          }
        }
        .lv {
          color: #fecd4a;
        }
      }
    }
    .nav-right {
      margin-left: auto;
      display: flex;
      align-items: center;
      .follow {
        width: 70px;
        height: 30px;
        line-height: 30px;
        border-radius: 15px;
        background-color: #ffc536;
        text-align: center;
        font-size: 14px;
        font-weight: bold;
      }
      .more {
        font-size: 20px;
        font-weight: bold;
        margin-left: 10px;
      }
    }
  }
  .main {
    .banner {
      display: flex;
      justify-content: center;
      width: 100%;
      position: relative;
      .my-swipe {
        width: 100%;
        display: flex;
        align-items: center;
      }
      .banner-text {
        position: absolute;
        left: 20px;
        bottom: 20px;
        padding: 3px 5px;
        font-size: 14px;
        background-color: #ffc536;
      }
    }
    .title {
      margin-top: 10px;
      padding: 10px;
      font-weight: bold;
      font-size: 18px;
    }
  }
  .content {
    padding: 10px;
    .content-item {
      font-size: 18px;
      line-height: 30px;
    }
  }
  .date-box {
    padding: 10px;
    display: flex;
    color: #666;
    font-size: 12px;
    .likes {
      margin-left: auto;
    }
  }
  .input-comment {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f5f5f5;
    height: 50px;
    display: flex;
    align-items: center;
    padding: 0 10px;
    .input-box {
      width: 120px;
      .input {
        width: 120px;
        outline: none;
        border: none;
        background-color: #ddd;
        border-radius: 30px;
        padding: 8px 15px;
        font-size: 14px;
      }
    }
    .comment-count {
      margin-left: auto;
      display: flex;
      .comment-img {
        width: 20px;
        height: 20px;
        margin-right: 5px;
      }
    }
    .likes-box {
      display: flex;
      margin: 0 5px;
      align-items: center;
      .likes-img {
        width: 24px;
        height: 24px;
        margin-right: 5px;
      }
    }
    .collect-box {
      display: flex;
      .collect-img {
        width: 24px;
        height: 24px;
        margin-right: 5px;
      }
    }
  }
  .related-notes-title {
    padding: 10px;
    font-size: 18px;
  }
  .related-notes {
    margin-left: 5px;
    .banner {
      width: 100%;
    }
    .title {
      padding: 10px 10px 0;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    .user-likes {
      padding: 10px 5px;
      display: flex;
      .user {
        display: flex;
        align-items: center;
        .head-img {
          width: 20px;
          height: 20px;
          border-radius: 50%;
          margin-right: 5px;
          overflow: hidden;
        }
        .name {
          font-size: 12px;
        }
      }
      .likes {
        margin-left: auto;
        display: flex;
        align-items: center;
        .likes-img {
          width: 20px;
          height: 20px;
          margin-right: 5px;
        }
        .likes-count {
          font-size: 12px;
        }
      }
    }
  }
}
</style>
